@media (max-width:992px) {
    .organizer-image {
        /* background-color: #ddd; */
        width: 110px;
        height: 110px;
        overflow: hidden;
        border-radius: 100%;
        /* margin: 0 auto 10px auto; */
        object-fit: contain;

        img {
            z-index: -1;
            border-radius: 100%;
            /* background-color: red; */
            object-fit: contain;
        }


    }

    .register-button {
        margin-top: 5%;
        padding: 10px 180px;
        font-size: 1.5rem;
    }

}

@media (max-width:768px) {
    .rules-title {
        font-size: 3rem;
        font-weight: 600;
        color: rgb(5, 133, 82);
        margin: 0;
    }

    .register-button {
        background-color: #fff;
        color: #000;
        border: none;
        padding: 10px 180px;
        font-size: 1.5rem;
        border-radius: 20px;
        cursor: pointer;
        font-weight: 600;
        letter-spacing: 2px;
        margin-top: 30px;
        margin-left: -2% !important;
    }
}

@media (max-width:576px) {
    .rules-title {
        font-size: 3rem;
        font-weight: 600;
        color: rgb(5, 133, 82);
        margin: 0;
    }

    .organizer-image {
        /* background-color: #ddd; */
        width: 120px;
        height: 120px;
        overflow: hidden;
        border-radius: 100%;
        margin: 0 auto 10px auto;
        object-fit: contain;

        img {
            z-index: -1;
            border-radius: 100%;
            /* background-color: red; */
            object-fit: contain;
        }


    }

    .register-button {
        background-color: #fff;
        color: #000;
        border: none;
        padding: 10px 180px;
        font-size: 1.5rem;
        border-radius: 20px;
        cursor: pointer;
        font-weight: 600;
        letter-spacing: 2px;
        margin-top: 30px;
        margin-left: -2% !important;
    }

}

@media (max-width:420px) {

    .rules-title {
        font-size: 2.1rem;
        font-weight: 600;
        color: rgb(255, 128, 0);
        /* margin-left: 15%; */
        /* margin-bottom: 5%; */
    }

    .rules-section {
        list-style: none;
        /* word-break: break-all; */

        .prizes {

            font-size: 1.5rem;
            width: fit-content;

            span {
                font-size: 1.5rem;
                color: rgb(255, 128, 0);
            }
        }

        .venue {
            margin-top: 40px;
            font-weight: 100;

            span {
                font-weight: 600;
                color: rgb(255, 128, 0);
                font-size: 1.5rem;
            }
        }


        .timing {

            .time {
                font-weight: 100;
                font-size: 0.8rem;

                span {
                    font-size: 1.5rem;
                    font-weight: 600;

                }
            }

            .date {
                margin-top: 15px;
                font-weight: 100;
                font-size: 0.8rem;

                span {
                    font-size: 1.5rem;
                    font-weight: 600;
                }
            }

            .note {
                font-weight: 100;
                font-size: 0.8rem;

                span {
                    font-size: 1.5rem;
                    font-weight: 600;
                }
            }
        }

    }

    .organizers-section {

        /* background-color: #ddd; */
        width: 100vw;
        margin: 0;

        margin-bottom: 10%;

        h2 {
            width: fit-content;
            margin-top: 10%;
            margin-bottom: 10%;
            font-size: 1.8rem;
        }

        img {
            border-radius: 100%;
        }
    }

    .organizer-list {
        /* display: flex;
        justify-content: center;
        flex-direction: column; */
        gap: 10px;
    }

    .organizer-image {
        background-color: #ddd;
        width: 100px;
        height: 100px;

        border-radius: 50%;
        margin: 0 0 10px auto;
    }

    .register-button {
        background-color: #fff;
        color: #000;
        border: none;
        padding: 8px 110px;
        font-size: 1.7rem;
        font-weight: 600;
        border-radius: 20px;
        cursor: pointer;
        letter-spacing: 2px;
        margin-top: 20px 0 0 -10%;
    }
}