@media (max-width:992px) {
    .glimpse-main {
        /* background-color: red; */
        margin-top: 15% !important;
    }

    .glimpse-div {
        .glimp-stvya {
            /* background-color: red; */
            width: 10%;
            margin: 0.7% 0 0 -4% !important;
        }

        .glimp-dp-02 {
            width: 18%;
            margin: 1% -6% 0 -1%;
            /* background-color: rebeccapurple; */
        }

        .stvya-glimp {
            /* background-color: red; */
            width: fit-content;
            margin: 0 0 0 0;
        }

        .glipm-line-01 {
            width: 30%;
            margin: 0 0 0 -2%;
        }
    }

    .M-button {
        .arrow {
            font-size: 28px !important;
        }

        .button-text {
            font-size: 1rem !important;
            font-weight: 600;
        }
    }


    

}

@media (max-width:768px) {
    .glimpse-main {
        /* background-color: red; */
