                font-size: 20px;
            }

            .button-text {
                font-size: 1rem;
                font-weight: 600;
            }
        }

        .glimpse-div {
            .glimp-stvya {
                /* background-color: red; */
                width: 15%;
                margin: 0 -6% 0 -6%;
            }

            .glimp-dp-02 {
                width: 20%;
                margin: 1% -6% 0 -1%;
                /* background-color: rebeccapurple; */
